@charset "UTF-8";
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);
* {
  font-family: "微軟正黑體";
  box-sizing: border-box; }

html, body {
  margin: 0;
  padding: 0; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

.default {
  max-width: 1024px;
  margin-right: auto;
  margin-left: auto;
  padding: 0px 42px; }

.phone {
  display: none; }

header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #fff; }
  header .container {
    display: flex;
    align-items: center;
    height: 100px; }
    header .container img {
      height: 40px; }
    header .container ul {
      display: flex;
      margin-left: auto;
      height: 100%; }
      header .container ul a {
        display: inline-flex;
        height: 100%;
        align-items: center;
        padding: 0px 30px;
        text-decoration: none;
        color: #3F5D45;
        font-weight: bold;
        font-size: 18px; }
        header .container ul a:hover {
          background-color: #3F5D45;
          color: white; }
    header .container .cart {
      margin-left: 49px; }
    header .container .material-icons {
      font-size: 24px;
      color: #3F5D45;
      cursor: pointer; }
      header .container .material-icons:hover {
        color: #8DA291; }
  header .burger {
    position: relative;
    width: 24px;
    height: 24px;
    cursor: pointer; }
    header .burger .burger1, header .burger .burger2, header .burger .burger3 {
      position: absolute;
      height: 2px;
      width: 18px;
      border-radius: 4px;
      background-color: #3F5D45;
      top: 5px;
      left: 10%;
      transition: 350ms; }
    header .burger .burger2 {
      top: 10px; }
    header .burger .burger3 {
      top: 15px; }
    header .burger.active .burger1 {
      transform: translateY(250%) rotate(45deg); }
    header .burger.active .burger2 {
      background-color: transparent; }
    header .burger.active .burger3 {
      transform: translateY(-250%) rotate(-45deg); }

@media all and (max-width: 768px) {
  header .container {
    position: relative;
    padding: 0px 30px;
    height: 84px;
    justify-content: space-between; }
    header .container .phone img {
      height: 17.74px; }
    header .container ul {
      position: absolute;
      top: 84px;
      left: 0;
      max-height: 0;
      height: auto;
      visibility: hidden;
      overflow: hidden;
      background-color: #fff;
      width: 100%;
      flex-wrap: wrap;
      transition: 500ms; }
      header .container ul li {
        width: 100%; }
        header .container ul li a {
          padding: 20px 0px;
          display: block;
          text-align: center; }
      header .container ul.active {
        max-height: 200px;
        visibility: visible; }
    header .container .cart {
      margin: 0; } }

main {
  width: 100%;
  height: auto;
  padding-top: 100px;
  padding-bottom: 60px; }
  main .sm_container {
    padding: 0px 122px;
    padding-top: 30px;
    display: flex;
    align-items: center; }
  main .wrapper {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center; }
    main .wrapper h1, main .wrapper h2 {
      text-align: center; }
    main .wrapper h1 {
      color: #EAF0ED;
      margin-bottom: 40px;
      font-size: 36px; }
    main .wrapper h2 {
      font-size: 24px;
      color: #8DA291;
      margin-bottom: 36px;
      font-weight: normal; }
    main .wrapper .input:not(:last-child) {
      margin-bottom: 16px; }
    main .wrapper .input {
      display: flex;
      align-items: center;
      background-color: #EAF0ED;
      height: 56px; }
      main .wrapper .input .material-icons {
        color: #3F5D45;
        margin: 0px 16px; }
      main .wrapper .input input {
        background-color: transparent;
        width: 70%;
        border: none;
        outline: none; }
    main .wrapper .checkBox {
      display: flex;
      align-items: center; }
      main .wrapper .checkBox input {
        display: none; }
        main .wrapper .checkBox input:checked ~ .check .material-icons {
          display: inline-block; }
      main .wrapper .checkBox label {
        color: #EAF0ED;
        cursor: pointer;
        font-size: 16px; }
      main .wrapper .checkBox .check {
        display: inline-block;
        width: 16px;
        height: 16px;
        background-color: #EAF0ED;
        margin-right: 8px; }
        main .wrapper .checkBox .check .material-icons {
          font-size: 16px;
          color: #3F5D45;
          font-weight: bold;
          display: none; }
    main .wrapper .btn {
      width: 100%;
      height: 65px;
      background-color: #FFE180;
      color: #3F5D45;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 24px;
      font-weight: bold;
      cursor: pointer; }
      main .wrapper .btn span {
        position: relative;
        transition: border 200ms; }
        main .wrapper .btn span:before {
          content: '';
          position: absolute;
          bottom: -2px;
          display: inline-block;
          width: 0%;
          height: 2px;
          background-color: #3F5D45; }
      main .wrapper .btn:hover span:before {
        animation: border 700ms;
        width: 100%; }
    main .wrapper .socialLogin:not(:last-child) {
      margin-bottom: 16px; }
    main .wrapper .socialLogin {
      width: 100%;
      height: 56px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #fff; }
      main .wrapper .socialLogin img {
        width: 105px; }
      main .wrapper .socialLogin .img {
        position: relative; }
        main .wrapper .socialLogin .img:before {
          content: '';
          position: absolute;
          bottom: -3px;
          display: inline-block;
          width: 0%;
          height: 2px;
          background-color: #8DA291; }
      main .wrapper .socialLogin:hover .img:before {
        animation: border 700ms;
        width: 100%; }
  main .left {
    background-color: #3F5D45; }
    main .left .insideBox {
      width: 100%;
      padding: 40px 30px 25px 30px; }
    main .left .inputBox {
      margin-bottom: 16px; }
  main .right {
    background-color: #EAF0ED;
    height: 350px;
    padding: 40px 30px; }

@keyframes border {
  from {
    width: 0%; }
  to {
    width: 100%; } }

footer {
  width: 100%; }
  footer .content {
    max-width: 1024px;
    margin-right: auto;
    margin-left: auto;
    padding: 0px 122px;
    display: flex; }
  footer .email {
    background-color: #3F5D45;
    height: 116px; }
    footer .email .content {
      height: 100%;
      justify-content: space-between;
      align-items: center; }
    footer .email .title {
      display: flex;
      align-items: center; }
      footer .email .title img {
        width: 40px;
        height: 40px;
        margin-right: 20px; }
      footer .email .title span {
        font-size: 24px;
        color: #EAF0ED;
        letter-spacing: normal; }
    footer .email .input {
      display: flex;
      align-items: center;
      background-color: #EAF0ED;
      max-width: 380px;
      width: 60%;
      height: 56px; }
      footer .email .input .material-icons {
        color: #3F5D45;
        margin: 0px 16px; }
      footer .email .input input {
        background-color: transparent;
        width: 70%;
        border: none;
        outline: none; }
      footer .email .input .setBtn {
        position: relative;
        background-color: #FFE180;
        align-self: stretch;
        margin-left: auto;
        width: 56px;
        height: 56px;
        cursor: pointer;
        overflow: hidden; }
        footer .email .input .setBtn:hover .material-icons {
          animation: sentMail 1.2s infinite; }
        footer .email .input .setBtn .material-icons {
          position: absolute;
          top: 50%;
          transform: translateY(-50%); }
  footer .footerInfo {
    height: 341px;
    background-color: #EAF0ED; }
    footer .footerInfo .content {
      padding: 30px 122px;
      display: flex;
      height: 100%;
      justify-content: space-between; }
    footer .footerInfo .left {
      display: flex;
      flex-direction: column; }
      footer .footerInfo .left > img {
        margin-bottom: auto;
        width: 171.22px; }
      footer .footerInfo .left > p {
        color: #3F5D45;
        font-weight: bold;
        line-height: 1.8; }
    footer .footerInfo .social {
      margin-top: 30px; }
      footer .footerInfo .social a {
        display: inline-block; }
      footer .footerInfo .social a:not(:last-child) {
        margin-right: 8px; }
      footer .footerInfo .social img {
        width: 32px;
        height: 32px; }
    footer .footerInfo .right {
      display: flex;
      flex-direction: column; }
      footer .footerInfo .right > img {
        width: 59.28px;
        margin-bottom: auto;
        margin-left: auto; }
      footer .footerInfo .right > p {
        color: #3F5D45;
        font-family: serif; }

@keyframes sentMail {
  from {
    left: -70%; }
  to {
    left: 100%; } }

@media all and (max-width: 768px) {
  footer .content {
    flex-direction: column; }
  footer .email {
    height: 186px; }
    footer .email .content {
      padding: 34px 30px 30px 30px; }
    footer .email .input {
      width: 100%; }
  footer .footerInfo {
    height: 311px; }
    footer .footerInfo .content {
      padding: 38.9px 30px 30px 30px; }
    footer .footerInfo .left > img {
      margin-bottom: 39.5px; }
    footer .footerInfo .left > p {
      line-height: 1.5; }
    footer .footerInfo .left .social {
      margin-top: 37px; }
    footer .footerInfo .right img {
