@media all and (max-width: 768px) {
  .pc {
    display: none; }
  .phone {
    display: block; }
  .default {
    width: 100%;
    padding: 0; }
  main {
    padding-top: 84px;
    padding-bottom: 0px; }
    main .sm_container {
      padding: 0; }
    main .wrapper {
      width: 100%; }
      main .wrapper h1 {
        margin-bottom: 30px; }
    main .left .social {
      display: flex;
      background-color: #EAF0ED;
      margin-bottom: 30px;
      margin-left: -30px;
      margin-right: -30px;
      height: 116px;
      padding: 30px; }
    main .left .socialItem:not(:last-child) {
      border-right: 1px solid #EAF0ED; }
    main .left .socialItem {
      width: calc(100% / 3);
      background-color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer; }
      main .left .socialItem img {
        width: 75px; }
    main .right {
      display: none; } }
